
import React from "react";
import { Component } from "react";


export class Header extends Component {
    
    state = {
        isOpen: false
    }

    toggleMenu = () => {
        let isOpen = !this.state.isOpen;
        this.setState({ isOpen })
    }

    render() {
        return (
            <section className="main-container">
                <div className="main-header flex">
                    <div className="left-nav flex align-center">
                        <div className="logo">Shortly</div>
                    </div>
                    <div className={(!this.state.isOpen) ? "main-nav flex sb align-center" : "main-nav flex sb align-center menu-open"} onClick={() => this.toggleMenu()}>
                        <div className="center-nav flex  align-center">
                            <div >Featurs</div>
                            <div >Pricing</div>
                            <div >Resources</div>
                        </div>
                        <div className="right-nav flex sb align-center" >
                            <div>login</div>
                            <button className="signup">sign-up</button>
                        </div>
                    </div>
                    <div className="mobile-menu" onClick={() => this.toggleMenu()}>☰</div>
                </div>
            </section>
        )
    }
}
