import React from 'react'
import { Link } from "react-router-dom";
import InstagramIcon from '@material-ui/icons/Instagram';
import FacebookIcon from '@material-ui/icons/Facebook';
import TwitterIcon from '@material-ui/icons/Twitter';
import PinterestIcon from '@material-ui/icons/Pinterest';

export function Footer() {
    return (
        <section className="main-container footer-container">
            <div className="flex footer-p">
                <div className="footer-logo">
                    <h2>Shortly</h2>
                </div>
                <div className="footer">
                    <div className="foot-col">
                        <div className="bold"><p>Features</p></div>
                        <p>Link Shortening</p>
                        <p>Branded Links</p>
                        <p>Analytics</p>
                    </div>
                    <div className="foot-col">
                        <div className="bold"><p>Resources</p></div>
                        <p>Blog</p>
                        <p>Developers</p>
                        <p>Support</p>
                    </div>
                    <div className="foot-col">
                        <div className="bold"><p>Company</p></div>
                        <p>About</p>
                        <p>Our Team</p>
                        <p>Careers</p>
                        <p>Our Team</p>
                    </div>
                    <div className="flex sb mt30">
                        <div className="icon"><FacebookIcon /></div>
                        <div className="icon"><TwitterIcon /></div>
                        <div className="icon"><PinterestIcon /></div>
                        <div className="icon"><InstagramIcon /></div>
                    </div>
                </div>
            </div>
        </section>
    )
}
