import React from "react";
import { Component } from "react";
import { Search } from "./Search"



export class Statistic extends Component {
    state = {
        isOpen: false,
        isCopied: [false, false, false],
        field:'',
        error:''
    }

    toogleLinks = () => {
        this.setState({ isOpen: !this.state.isOpen })
    }

    doCopied = (idx) => {
        let copied = this.state.isCopied;
        copied[idx] = true;
        this.setState({ isCopied: copied });
    }
    handleChange =(ev) =>{
        let field = ev.target.value;
        this.setState({field})
    }

    checkErr = (ev) =>{
        ev.preventDefault();
        const field= this.state.field;
        (field === '')? this.setState({error : 'Please add a link'}): this.setState({error : ''}) ;
    }

    render() {
        return (
            <section className="stat">
                <div className="main-container ad-bgc">
                    <Search toogleLinks={this.toogleLinks}
                            handleChange={this.handleChange}
                            checkErr={this.checkErr}
                            field={this.state.field}
                            error={this.state.error} />
                    {(this.state.isOpen) ?
                        <div className="flex column search-res">
                            <div className="flex sb res align-center">
                                <div className="res-1">https://www.frontendmentor.io</div>
                                <div className="res-2 flex sa">
                                    <div>https://rel.ink/k4lKyk</div>
                                    <button className={(!this.state.isCopied[0]) ? "btn1" : "btn2"} onClick={() => this.doCopied(0)}>{(!this.state.isCopied[0]) ? 'Copy' : 'Copied!'}</button>
                                </div>
                            </div>
                            <div className="flex sb res align-center">
                                <div className="res-1">https://twitter.com/frontendmentor</div>
                                <div className="res-2 flex sa">
                                    <div>https://rel.ink/gxOXp9</div>
                                    <button className={(!this.state.isCopied[1]) ? "btn1" : "btn2"} onClick={() => this.doCopied(1)}>{(!this.state.isCopied[1]) ? 'Copy' : 'Copied!'}</button>
                                </div>
                            </div>
                            <div className="flex sb res align-center">
                                <div className="res-1">https://www.linkedin.com/company/frontend-mentor</div>
                                <div className="res-2 flex sa">
                                    <div>https://rel.ink/gob3X9</div>
                                    <button className={(!this.state.isCopied[2]) ? "btn1" : "btn2"} onClick={() => this.doCopied(2)}>{(!this.state.isCopied[2]) ? 'Copy' : 'Copied!'}</button>
                                </div>
                            </div>
                        </div>
                        : ''
                    }
                    <div className="st-text flex column">
                        <h2 className="ad-st">Advanced Statistics</h2>
                        <p className="ad-p">Track how your links are performing across the web with our advanced statistics dashboard. </p>
                    </div>
                    <div className="st-cards">
                        <div className="st-card1">
                            <div className="oval flex align-center justify-center"><img className="st-card-img" src="https://res.cloudinary.com/dygtul5wx/image/upload/v1616306862/exproj2/new/icon-detailed-records_lyjawc.png" /></div>
                            <div className="flex column sb st-txt">
                                <div className="txt1-st">Brand Recognition</div>
                                <p className="txt2-st">Boost your brand recognition with each click. Generic links don’t mean a thing. Branded links help instil confidence in your content.</p>
                            </div>
                        </div>
                        <div className="green-line"></div>
                        <div className="st-card2">
                            <div className="oval flex align-center justify-center"><img className="st-card-img" src="https://res.cloudinary.com/dygtul5wx/image/upload/v1616306862/exproj2/new/icon-brand-recognition_eluws4.png" /></div>
                            <div className="flex column sb st-txt">
                                <div className="txt1-st">Detailed Records</div>
                                <p className="txt2-st">Gain insights into who is clicking your links. Knowing when and where people engage with your content helps inform better decisions.</p>
                            </div>
                        </div>
                        <div className="green-line"></div>
                        <div className="st-card3">
                            <div className="oval flex align-center justify-center"><img className="st-card-img" src="https://res.cloudinary.com/dygtul5wx/image/upload/v1616306863/exproj2/new/icon-fully-customizable_mg3xte.png" /></div>
                            <div className="flex column sb st-txt">
                                <div className="txt1-st">Fully Customizable</div>
                                <p className="txt2-st">Improve brand awareness and content discoverability through customizable links, supercharging audience engagement.</p>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
        )
    }
}