import React from "react";


export function Jumbo(props) {
  return (
    <section>
      <div className="main-container jumbo-container">
        <div className="jumbo flex column sb">
          <div className="slogan">
            <div>More Than Just</div>
            <div>Shorting Links</div>
          </div>
          <p className="short-explanation">Build your brand’s recognition and get detailed insights on how your links are performing.</p>
          <div className="jumbo-btn-small-screen">
            <button className="jumbo-btn"><span>Get Started</span></button>
          </div>
        </div>
        <div className="jumbo-img"><img src="https://res.cloudinary.com/dygtul5wx/image/upload/v1603785840/exproj2/illustration-working_clqo3o.svg" /></div>
      </div>
    </section >
  );
}
