import React from "react";

export function Search({ toogleLinks, checkErr, handleChange, field, error }) {
    return (
        <section>
            <form className="search-bar flex sa align-center" onSubmit={checkErr}>
                <img src="https://res.cloudinary.com/dygtul5wx/image/upload/v1616306862/exproj2/new/bg-shorten-desktop_hostsg.png" />
                <input className={(error !== '')?'error-input':'input'}
                 type="text"
                    placeholder="Shorten a link here..."
                    value={field}
                    onChange={handleChange} />
                <button className="search-btn" onClick={toogleLinks}>Shorten It!</button>
                {(error !== '') ?
                    <div className="error">{error}</div> : ''}
            </form>
        </section>
    )
}
